import { cn } from '@/lib/utils';
import React, { useCallback } from 'react';

interface CircularProgressProps {
    percentage?: number;
    size?: number;
    textClassName?: string;
    progressClassName?: string;
    backgroundClassName?: string;
}

const CircularProgress = ({
    percentage = 60,
    size = 100,
    textClassName = 'text-primery-blue-400',
    progressClassName = 'stroke-primery-blue-400',
    backgroundClassName='stroke-secondary-blue-500'
}: CircularProgressProps) => {
    const radius = size / 2 - 4; // Account for stroke width
    const circumference = 2 * Math.PI * radius;

    const getProgressOffset = useCallback(() => {
        return circumference - (percentage / 100) * circumference;
    }, [circumference, percentage]);

    return (
        <div className="relative inline-block" style={{ width: size, height: size }}>
            <svg className="transform -rotate-90" width={size} height={size}>
                {/* Background circle */}
                <circle
                    cx={size / 2}
                    cy={size / 2}
                    r={radius}
                    className={cn('fill-none', backgroundClassName)}
                    strokeWidth="4"
                />

                {/* Progress circle */}
                <circle
                    cx={size / 2}
                    cy={size / 2}
                    r={radius}
                    className={cn('fill-none transition-all duration-500', progressClassName)}
                    strokeWidth="4"
                    strokeLinecap="round"
                    strokeDasharray={circumference}
                    strokeDashoffset={getProgressOffset()}
                />
            </svg>

            {/* Percentage text */}
            <div className="absolute inset-0 flex items-center justify-center">
                <span className={cn('font-medium leading-[22.5px]', textClassName)} style={{ fontSize: size * 0.24 }}>
                    {percentage}%
                </span>
            </div>
        </div>
    );
};

export default CircularProgress;
